import * as React from "react";
import { Flex, ListView, Calendar, Toast } from 'antd-mobile'
import moment from 'moment'
import $ from 'jquery'

//redux 
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as Actions from '../../actions';
import { getAnswerList } from '../../api/answer'
import { getUserInfo } from '../../api/bind'
import AnswerItem from '../../components/answerItem'

import Blank from '../../components/blank/blank'
import search from '../../assets/img/top/search.png'

import './answer.less'


const mapStateToProps = (state: any) => {
  return {
    store: state
  }
}

const mapDispatchToProps = (dispatch: any) => {
  return {
    ...bindActionCreators(Actions, dispatch)
  }
}

interface ISprops {
  [propName: string]: any;
}
interface IState {
  hasMore: boolean,
  isLoading: boolean,
  dataSource: any,
  filter: any,
  calendarShow: boolean,
}
class Answer extends React.Component<ISprops, IState> {
  constructor(props) {
    super(props)
    const dataSource = new ListView.DataSource({
      rowHasChanged: (row1, row2) => row1 !== row2,
    });
    this.state = {
      hasMore: false,
      isLoading: false,
      dataSource,
      filter: {
        start_time: '',
        end_time: '',
      },
      calendarShow: false,
    }
  }
  courseList: object[] = [];
  page_num: number = 1;
  page_size: number = 6;
  componentWillMount() {
    $('.list-view-section-body').addClass('clearfix');
  }
  checkUser () {
    Toast.loading('')
    getUserInfo({}).then( res => {
        Toast.hide()
        if (!res.status) {
          if(res.data.user_type !== 2 && res.data.user_type !== 3) {
            this.props.history.push('/card/index')
          } else {
            this.getPageList()
          }
        } 
    })
  }
  componentDidMount() {
    document.title = '';
    this.checkUser()
    
  }
  onEndReached = () => {
    //
    if (this.state.isLoading || !this.state.hasMore) {
      return;
    }
    this.getPageList()
  }
  handleFilterHide = () => {
    this.setState({ calendarShow: false })
  }
  // 
  updateCalendar = (startDateTime, endDateTime) => {
    this.setState({
        filter:{
          start_time: startDateTime ? moment(startDateTime).format("YYYY-MM-DD") : startDateTime,
          end_time: endDateTime ? moment(endDateTime).format("YYYY-MM-DD") : endDateTime,
        }
    },() => {
      this.handleFilterHide()
      this.page_num = 1;
      this.getPageList()
    })
  }
  handleClearTime = () => {
    //
    this.updateCalendar(
        null,
        null,
    )
}
  getPageList = () => {
    Toast.loading('')
    this.setState({ isLoading: true });
    const { filter } = this.state;
    let { page_num, page_size } = this;
    getAnswerList({
      page_num,
      page_size,
      file_type: 1,
      ...filter
    }).then(res => {
      Toast.hide()
      if (!res.status) {
        let { list, total_pages, page_num } = res.data;
        // 
        if (page_num === 1) {
          this.courseList = []
        }
        this.courseList = [...this.courseList, ...list];

        // 1
        if (page_num < total_pages) {
          this.page_num += 1
        }

        this.setState({
          dataSource: this.state.dataSource.cloneWithRows(this.courseList),
          isLoading: false,
          hasMore: page_num < total_pages
        })
      }
    })
  }
  render() {
    const { calendarShow,isLoading } = this.state;
    const { start_time, end_time } = this.state.filter;
    const { courseList } = this;
    const renderFooter = () => (
      this.state.hasMore
        ?
        <div style={{ textAlign: 'center', lineHeight: '.26rem' }}>{this.state.isLoading ? 'Loading...' : 'Loaded'}</div>
        :
        <div></div>
    )
    const Row = (rowData, sectionID, rowID) => (
      <AnswerItem key={rowID} course={rowData}/>
    )
    return (
      <div className="answer">
        <div className="answer-header">
          <Flex>
            <Flex.Item onClick={() => this.setState({ calendarShow: !calendarShow })} >
               < img src={search} alt="" />
            </Flex.Item>
          </Flex>
        </div>
        {/*  */}
        <Calendar
          rowSize="xl"
          defaultValue={start_time ? [new Date(start_time), new Date(end_time)] : undefined}
          visible={calendarShow}
          onCancel={this.handleFilterHide}
          onConfirm={this.updateCalendar}
          onClear={this.handleClearTime}
        />
        {
          courseList.length === 0 && !isLoading ?
            <Blank/>
            :
            <ListView
            dataSource={this.state.dataSource}
            renderFooter={courseList.length === 0 ? () => (<div></div>) : renderFooter}
            renderRow={Row}
            className="answer-detail clearfix"
            pageSize={4}
            useBodyScroll
            scrollRenderAheadDistance={500}
            onEndReached={this.onEndReached}
            onEndReachedThreshold={10}
          />
        }

      </div >
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Answer);