import * as React from "react";

import { getAnswerInfo } from '../api/answer'

import pdflogo from '../assets/img/pdflogo.png'
import pdf from '../assets/img/pdf.png'
import down from '../assets/img/down.png'
import picnew from '../assets/img/new.png'
import '../container/answer/answer.less'


interface ISprops {
    [propName: string]: any,
}
interface IState {
}
export default class AnswerItem extends React.Component<ISprops, IState> {
    componentWillMount() {
    }
    clickItem = (id,url) => {
        getAnswerInfo({
            id: id
        }).then( (data) => {
            if(!data.status) {
                window.location.href = url;
            }
        })
    }

    render() {
        const { course } = this.props;
        return (
            <React.Fragment>
                <div className="answer-detail-i" onClick={() => this.clickItem(course.id,course.file_url)}>
                    {
                        course.is_new === 1 ?
                            <img src={picnew} className="new" alt="" />
                            :
                            <div></div>
                    }
                    <img src={pdflogo} alt="" />
                    <p className="time">
                        {course.name}
                    </p>
                    <p className="upload">
                        : {course.add_time}
                    </p>
                    <p className="clearfix bottom">
                        <span className="left">
                            <img src={pdf} alt="" />
                            {course.real_size}
                        </span>
                        <span className="left">
                            <img src={down} alt="" />
                            {course.visit_count}
                        </span>
                    </p>
                </div>
            </React.Fragment>
        )
    }

}
